<template>
    <div class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
        
    </div>
    <div class="container mt-5">
        <div class="row">
            <div class="col-md-12">
                <div class="card border-0 rounded shadow">
                    <div class="card-body"> 
                        <router-link :to="{name: 'transaksi_deposit_uang.create'}"
                            class="btn btn-md btn-success">TAMBAH TRANSAKSI DEPOSIT UANG</router-link>
                        <table class="table table-striped table-bordered mt-4">
                            <thead class="thead-dark">
                                <tr>
                                    <th scope="col">ID</th>
                                    <th scope="col">PEGAWAI</th>
                                    <th scope="col">MEMBER</th>                                 
                                    <th scope="col">BONUS</th>
                                    <th scope="col">JUMLAH PEMBELIAN DEPOSIT</th>
                                    <th scope="col">TANGGAL TRANSAKSI</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(transaksi_deposit_uang, id_transaksi_deposit_uang) in transaksi_deposit_uangs" :key="id_transaksi_deposit_uang">
                                    <td>{{ transaksi_deposit_uang.id_transaksi_deposit_uang}}</td>
                                    <td>{{ transaksi_deposit_uang.pegawai.nama}}</td>
                                    <td>{{ transaksi_deposit_uang.member.nama }}</td>
                                    <td>{{ transaksi_deposit_uang.promo_uang.bonus }}</td>
                                    <td>{{ transaksi_deposit_uang.jumlah_deposit_uang }}</td>
                                    <td>{{ transaksi_deposit_uang.tgl_pembelian }}</td> 
                                    <td>
                                        <button class="btn btn-sm btn-primary ml-1" @click="downloadPDF(transaksi_deposit_uang, (transaksi_deposit_uang.jumlah_deposit_uang + transaksi_deposit_uang.promo_uang.bonus))">STRUK</button>
                                    </td>                               
                                </tr>
                            </tbody> 
                        </table> 
                    </div> 
                </div> 
            </div> 
        </div> 
    </div> 
</template> 
<script> 
import axios from 'axios' 
import { onMounted, ref } from 'vue' 
import jsPDF from 'jspdf'

export default { 
    setup() { 
        //reactive state 
        let transaksi_deposit_uangs = ref([]) 
        //mounted 
        onMounted(() => {
            //get API from Laravel Backend 
            axios.get('https://api-gofit.200710947.com/api/transaksi_deposit_uang') 
            .then(response => { 
                //assign state posts with response data 
                transaksi_deposit_uangs.value = response.data.data 
            }).catch(error => {
                 console.log(error.response.data) 
            }) 
        })

        function downloadPDF(transaksi_deposit_uang, total){
            const pdf = new jsPDF({
                orientation: 'landscape',
                unit: 'cm',
                format: [11, 20]
            });
            pdf.text('Struk Transaksi Deposit Uang', 6,1);
            pdf.text('-----------------------------------------------------------------------------------------------------', 0.5,1.5);
            pdf.text('Go Fit', 0.5, 2);
            pdf.text("No Struk : "+ transaksi_deposit_uang.id_transaksi_deposit_uang, 13, 2);
            pdf.text('Jl. Centralpark No.10 Yogyakarta', 0.5, 3);
            pdf.text("Tanggal : "+ transaksi_deposit_uang.tgl_pembelian, 13, 3);
            pdf.text('', 0.5, 4);
            pdf.text("Member                                  : "+ transaksi_deposit_uang.member.id_member +" / " + transaksi_deposit_uang.member.nama, 0.5, 5);
            pdf.text("Deposit                                   : Rp."+ transaksi_deposit_uang.jumlah_deposit_uang, 0.5, 6);
            pdf.text("Bonus deposit                        : Rp." + transaksi_deposit_uang.promo_uang.bonus, 0.5, 7);
            pdf.text("Total deposit ditambahkan     : Rp." + total,0.5, 8);
            pdf.text("Total deposit Member          : Rp." + transaksi_deposit_uang.member.deposit_uang,0.5, 9);
            pdf.text("Kasir : "+ transaksi_deposit_uang.pegawai.id_pegawai +" / "+ transaksi_deposit_uang.pegawai.nama, 13,10)
            pdf.save("Struk Deposit Uang_" + transaksi_deposit_uang.id_transaksi_deposit_uang + ".pdf");
        }
        
            //return 
        return { 
            transaksi_deposit_uangs,
            downloadPDF,
         } } } 
    </script> 
<style> </style>